'use strict';
var React = require('react');
var Reflux = require('reflux');
var Link = require('react-router').Link;

var ArcgisLayersActions=require('../../../../actions/arcgisLayersActions.js');
var ResultList=require('./results.jsx');
var _=require('lodash');

/*The button to add the layer to the map*/



var SearchInput=React.createClass({displayName: "SearchInput",

	handleCLick:function(){
		this.setState(_.assign(this.state,{"query":this.refs.search_input.getDOMNode().value}));
		this.props.onSearch(this.state);
	},

	getInitialState: function() {

		return {'feature': true,'image':true,'map':true ,'start':0 ,'num':500};
	},

	checkOption:function(evnt){
		var newState={};
		newState[evnt.target.value]=evnt.target.checked;
		this.setState(newState);
	},


	render: function() {
		console.log("layers->search->search: Render EsriSearch");
		return(
			React.createElement("div", {className: "text-search-wrapper"}, 
			React.createElement("div", {className: "search-box"}, 
				React.createElement("button", {type: "submit", className: "search-button", onClick: this.handleCLick}, 
					React.createElement("i", {className: "fa fa-search"})
				), 
				React.createElement("input", {className: "keyword-search", type: "text", placeholder: "Search layer", ref: "search_input"})
			), 
			React.createElement("div", null, 

			
			React.createElement("div", {className: "layer-search-options"}, 
				React.createElement("ul", null, 
					React.createElement("li", null, 
						React.createElement("span", {className: "select"}, 
							React.createElement("input", {className: "glyphicon glyphicon-stop", type: "checkbox", value: "feature", onClick: this.checkOption, checked: this.state.feature})
						), 
						"Feature Service"
					), 
					React.createElement("li", null, 
						React.createElement("span", {className: "select"}, 
							React.createElement("input", {className: "glyphicon glyphicon-stop", type: "checkbox", value: "map", onClick: this.checkOption, checked: this.state.map})
						), 
						"Map Service"
					), 
					React.createElement("li", null, 
						React.createElement("span", {className: "select"}, 
							React.createElement("input", {className: "glyphicon glyphicon-stop", type: "checkbox", value: "image", onClick: this.checkOption, checked: this.state.image})
						), 
						"Image Service"
					)
				)
			)
			)
			)
			);
	}
});

/*
  Root Element input text + search list
  */
  module.exports  = React.createClass({displayName: "exports",
  	render: function() {
  		console.log("layers->search->search: Render Layer Search");
  		return (
  			React.createElement("div", null, 
  		React.createElement(SearchInput, React.__spread({},   this.props       )), 
  	this.props.services?   React.createElement(ResultList, React.__spread({},  this.props       )):null
  	)
  	);
  	}
  });
