'use strict';

var React = require('react');
var Reflux = require('reflux');


var AddButton= React.createClass({displayName: "AddButton",

	handleAdd:function(){
		this.props.onAddLayer(this.props.service);
	},

	render: function() {
		return(React.createElement("button", {disabled: this.props.disabled, 
			className: this.props.disabled?"btn btn-xs btn-default":"btn btn-xs btn-info", onClick: this.handleAdd}, "ADD"));
	}
});


var EsriService=React.createClass({displayName: "EsriService",

	componentWillReceiveProps: function(nextProps) {

	},



	render: function() {
		console.log("layers->search->resultList: Render EsriService");
		var service=this.props.service;
		debugger;
		return(

			React.createElement("li", null, 
			React.createElement("div", {className: "layer-wrapper"}, 

				React.createElement("div", {className: "thumbnail pull-left"}, 
				React.createElement("img", {width: "110px", height: "73px", src: "http://www.arcgis.com/sharing/content/items/"+service.id+"/info/"+service.thumbnail})
				), 

				React.createElement("div", {className: "layer-info"}, 
					React.createElement("div", {className: "title", "data-toggle": "Loing is required"}, service.title, " ", service.loginRequired?React.createElement("i", {className: "text-warning small"}, "Loing is required"):''
					), 
					React.createElement("div", {className: "type"}, service.type, "  - ", service.access), 

					React.createElement("div", {className: "add"}, 
					service.added?React.createElement("span", null, "Added"):null, 
					React.createElement(AddButton, React.__spread({className: "btn btn-apply"},  this.props, {disabled: ((service.loginRequired && !this.props.token)||service.added)?true:false}))
					)
				)

			)
			)
			)
	}

});

module.exports=React.createClass({displayName: "exports",
	render: function() {
		console.log("layers->search->resultList: Render EsriServiceList");
		return(
			React.createElement("div", null, 
			(this.props.error)?React.createElement("p", {className: "label label-warning"}, this.props.error.message):null, 

			React.createElement("ul", {className: "esri-result-list"}, 
				
				this.props.services.map(function(s){
					return( React.createElement(EsriService, React.__spread({},  this.props, {service: s})))
				}.bind(this)), 
				React.createElement("li", null, React.createElement("a", {onClick: this.moreResults}, "More results"))
				)
				)
				);
	}

});
